import React, { useEffect, useRef, useState } from "react";

type Role = "user" | "assistant";

type Message = {
  id: string;
  role: Role;
  content: string;
  image?: string;
};

const uid = () => Math.random().toString(36).slice(2);

const AppleAIPrompt: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: uid(),
      role: "assistant",
      content:
        "Welcome. Ask a question or drop an image. I will respond calmly.",
    },
  ]);

  const [input, setInput] = useState("");
  const [image, setImage] = useState<string | null>(null);
  const [sending, setSending] = useState(false);
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    scrollRef.current?.scrollTo({
      top: scrollRef.current.scrollHeight,
      behavior: "smooth",
    });
  }, [messages]);

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    const file = e.dataTransfer.files?.[0];
    if (!file || !file.type.startsWith("image/")) return;
    setImage(URL.createObjectURL(file));
  };

  const handlePaste = (e: React.ClipboardEvent) => {
    const item = Array.from(e.clipboardData.items).find((i) =>
      i.type.startsWith("image/")
    );
    if (!item) return;
    const file = item.getAsFile();
    if (!file) return;
    setImage(URL.createObjectURL(file));
  };

  const send = () => {
    if ((!input.trim() && !image) || sending) return;

    setMessages((m) => [
      ...m,
      {
        id: uid(),
        role: "user",
        content: input.trim(),
        image: image || undefined,
      },
    ]);

    setInput("");
    setImage(null);
    setSending(true);

    setTimeout(() => {
      setMessages((m) => [
        ...m,
        {
          id: uid(),
          role: "assistant",
          content: "Placeholder response. Wire your AI here.",
        },
      ]);
      setSending(false);
    }, 700);
  };

  return (
    <main style={{ minHeight: "100vh", overflow: "hidden" }}>
      <Background>
        <section style={styles.shell}>
          <div style={styles.card}>
            <header style={styles.header}>
              <h2 style={styles.title}>AI Assistant</h2>
              <p style={styles.subtitle}>Text or images. Calm responses.</p>
            </header>

            <div ref={scrollRef} style={styles.chat}>
              {messages.map((m) => (
                <div
                  key={m.id}
                  style={{
                    ...styles.message,
                    ...(m.role === "user"
                      ? styles.user
                      : styles.assistant),
                  }}
                >
                  {m.image && (
                    <img src={m.image} style={styles.chatImage} />
                  )}
                  {m.content}
                </div>
              ))}
            </div>

            <div
              style={styles.inputWrap}
              onDrop={handleDrop}
              onDragOver={(e) => e.preventDefault()}
            >
              {image && (
                <div style={styles.imagePreview}>
                  <img src={image} style={styles.previewImg} />
                  <button
                    style={styles.previewClose}
                    onClick={() => setImage(null)}
                  >
                    ×
                  </button>
                </div>
              )}

              <textarea
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onPaste={handlePaste}
                onKeyDown={(e) => {
                  if (e.key === "Enter" && !e.shiftKey) {
                    e.preventDefault();
                    send();
                  }
                }}
                placeholder="Type or drop an image…"
                style={styles.textarea}
              />

              <button
                onClick={send}
                disabled={sending}
                style={styles.button}
              >
                Send
              </button>
            </div>
          </div>
        </section>
      </Background>
    </main>
  );
};

export default AppleAIPrompt;

/* ---------------- BACKGROUND ---------------- */

const Background = ({ children }: any) => (
  <div
    style={{
      minHeight: "100vh",
      padding: 20,
      background:
        "linear-gradient(300deg, #bebeff, #cedae6, #e9edf2, #c2d6ff)",
      backgroundSize: "140% 140%",
      animation: "gradientAnimation 22s ease infinite",
    }}
  >
    {children}
    <style>
      {`
        @keyframes gradientAnimation {
          0% { background-position: 0% 50%; }
          50% { background-position: 100% 50%; }
          100% { background-position: 0% 50%; }
        }
      `}
    </style>
  </div>
);

